.body{
  background-color:#ffe6e6;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.high{
  height: 500px;
}
.root{
  width: 100%;
}
.bton{  
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 2vw;
    line-height: 1.5;
    border-radius: .4rem;
    font-weight:bolder;
    transition:color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    color: #ffe6e6;
    background-color: #a685e2;
    border-color: #a685e2;
}

.bton:hover{
  color:#ffe6e6;
  font-weight:bolder;
  background-color:#6155a6;
  border-color:#6155a6}
.bton.focus,.bton:focus{
  color:#ffe6e6;
  background-color:#6155a6;
  border-color:#6155a6;
}
          
.box{
  background-image: radial-gradient(circle, #ffe6e6, #fdd0d8, #f7bad2, #e9a6d2, #d495d7, #c18bd6, #ac81d4, #9479d3, #8770c8, #7a67bc, #6e5eb1, #6155a6);
  border-style: solid;
  border-radius: 20px;
  border-width: 0;
  height: 100%;
}
.footer-box{
  color: #ffe6e6;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 2.3vw;
  background-color:#a685e2 ;
  border-style: solid;
  border-radius: 1rem;
  border-width: 0;
  height: 100%;
}
.header-box{
  color: #ffe6e6;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 3vw;
  background-color:#a685e2 ;
  border-style: solid;
  border-radius: 1rem;
  border-width: 0;
  height: 100%;
}
